:root {
  --primary: #623caf;
  --secondary: #32385D;
  --tertiary: #0f0533;
  --gradient: linear-gradient(90deg, #8cd6c8 0%, #9FACE6 100%);
  
  --revGradient:linear-gradient(119deg, #74EBD5 0%, #9FACE6 100%);
  
  
  --light: #fafafa;
  --dark: #0f0a11;
  --darker: #0a0a0a;
  --error: rgb(228, 46, 1);
  --spacing-one: calc(2.5rem * 1.5);
  --spacing-two: calc(3rem);
  --spacing-five: calc(1rem);

}

